.prizes-text {
    position: relative;
    overflow: hidden;
  }

  .tba {
      font-size: 4rem;
      padding: 4rem;
  }
  
  .prizes-text::after {
    position: absolute;
    top: 50%;
    right:12%;
    overflow: hidden;
    width: 70rem;
    height: 3px;
    content: '\a0';
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 200% 200%;
    -webkit-animation: gradient 5s ease infinite;
            animation: gradient 5s ease infinite;
  }
  
  @media only screen and (max-width: 1200px)
  {
    .prizes-text {
        text-align: center !important;
    }
    .prizes-text::after {
        display: none;
    }
    .tba {
      text-align: center;
      font-size: 3.5rem;
      padding: 0;
    }
  }
  
  .flip-card-prize {
    background-color: transparent;
    width: 100%;
    height: 400px;
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }
  
  .flip-card-inner-prize {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: left;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
  }
  
  .flip-card-prize:hover .flip-card-inner-prize {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  
  .flip-card-front-prize, .flip-card-back-prize, .flip-card-front-sawo, .flip-card-front-raahee {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 20px;
  }
  
  .flip-card-front-prize {
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 300% 300%;
    -webkit-animation: gradient 8s ease infinite;
            animation: gradient 8s ease infinite;
    color: white;
    padding: 1rem;
  }

  .flip-card-front-sawo {
    background: #fcde50;
    background-size: 300% 300%;
    -webkit-animation: gradient 8s ease infinite;
            animation: gradient 8s ease infinite;
    color: white;
    padding: 1rem;
  }
  
  .flip-card-front-raahee {
    background: linear-gradient(135deg, rgba(56,29,89,1) 35%, rgba(252,139,139,1) 71%, rgba(252,195,112,1) 100%);
    background-size: 300% 300%;
    -webkit-animation: gradient 8s ease infinite;
            animation: gradient 8s ease infinite;
    color: white;
    padding: 1rem;
  }
  
  .flip-card-back-prize {
    background: black;
    border: 2px solid white;
    color: white;
    padding: 3rem;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    font-family: 'DM Sans', sans-serif;
  }
  
